import React from 'react';
import { Button, Modal, Form, InputGroup } from 'react-bootstrap';
import "../../../../../assets/scss/plugins/forms/react-select/_react-select.scss";
import { Search } from 'react-feather';



class WorkflowMasterModal extends React.Component {
    render() {
        return (
            <div>
                <Modal
                    show={this.props.show}
                    onHide={this.props.handleHide}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            {this.props.InsertUpdate} Workflow Master
                        </Modal.Title>
                    </Modal.Header>
                    
                    <Form onSubmit={this.props.handleSubmit} autoComplete="off">
                        <Modal.Body>
                        <Form.Group controlId="WorkflowName">
                                    <Form.Label>Workflow Name</Form.Label>
                                    <Form.Control type="text" placeholder="Enter Workflow Name" value={this.props.WorkflowName} onChange={this.props.changeWorkflowName} required />
                                </Form.Group>
                                <Form.Group controlId="ModuleType">
                                    <Form.Label>Module Type</Form.Label>
                                    <InputGroup>
                                        <Form.Control value={this.props.ModuleTypeID} onChange={this.props.handleChangeModuleTypeID} hidden />
                                        <Form.Control
                                            type="text"
                                            value={this.props.ModuleTypeDescription}
                                            onChange={this.props.handleChangeModuleTypeDescription}
                                            placeholder="Search Module Type"
                                            aria-describedby="inputGroupPrepend"
                                            required
                                            readOnly
                                        />
                                        <InputGroup.Prepend>
                                            <Button variant="info" size="sm" onClick={this.props.handleShowSearch}>
                                                <Search />
                                            </Button>
                                        </InputGroup.Prepend>
                                    </InputGroup>
                                </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHide}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit" disabled={this.props.isEnabled}>
                                {this.props.InsertUpdate}
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>

                <Modal
                    show={this.props.showdelete}
                    onHide={this.props.handleHideDelete}
                >
                    <Modal.Header closeButton>
                        <Modal.Title id="AddUpdate">
                            Delete Workflow Master
                        </Modal.Title>
                    </Modal.Header>
                    <Form onSubmit={this.props.handleDelete} autoComplete="off">
                        <Modal.Body>
                            <Form.Group controlId="description">
                                <h4>Are you sure you want to delete this record?</h4>
                                <Form.Control type="text" placeholder="Click Delete Button" value={this.props.WorkflowName} onChange={this.props.changeWorkflowName} readOnly />
                            </Form.Group>
                        </Modal.Body>

                        <Modal.Footer>
                            <Button variant="success" onClick={this.props.handleHideDelete}>
                                Cancel
                            </Button>
                            <Button variant="primary" type="submit">
                                Yes Delete
                            </Button>
                        </Modal.Footer>
                    </Form>
                </Modal>
            </div>
        );
    }
}

export default WorkflowMasterModal;