import React from "react";
import { connect } from "react-redux";
import { Helmet } from 'react-helmet';
import { Card, CardBody, CardHeader, CardTitle, Row, Col } from "reactstrap";
import Breadcrumbs from "../../../../components/@vuexy/breadCrumbs/BreadCrumb";
import ModuleTypeSearch from '../../SearchComponents/ModuleTypeSearch/ModuleTypeSearch';
import * as API from "../../../../utility/crud/app.crud";
import * as Date from "../../../../utility/dateAndTime";
import { toastr } from 'react-redux-toastr';
import WorkflowMasterTable from "./Tables/WorkflowMasterTable";
import WorkflowMasterModal from "./Modals/WorkflowMasterModals";

class WorkflowMaster extends React.Component {
    constructor(props, context) {
        super(props, context);

        this.state = {
            columns: [],
            data: [],
            filteredData: [],
            value: '',
            show: false,
            showdelete: false,
            ID: 0,
            WORKFLOWNAME: '',
            MODULEID: 0,
            CREATEDBYID: 1,
            LASTMODIFIEDBYID: 1,
            DATECREATED: Date.ShowDate(),
            DATELASTMODIFIED: Date.ShowDate(),
            InsertUpdate: '',

            opensearch: false,
            MODULETYPEDESCRIPTION: '',

            searchcolumns: [],
            searchdata: [],
            searchFilteredData: [],
            searchvalue: '',

        }

        this.handleShow = () => {
            this.setState({
                show: true,
                InsertUpdate: 'Insert'
            });
            this.clearData();
        }

        this.handleHide = () => {
            this.setState({
                show: false
            });
        }

        this.handleHideDelete = () => {
            this.setState({
                showdelete: false
            });
        }

        this.handleShowSearch = () => {
            this.setState({ opensearch: true });
        };

        this.handleHideSearch = () => {
            this.setState({ opensearch: false });
        };

        this.populateData();
        this.populateTypes();

    }

    populateData() {
        const getdata = `
            query{
                WorkflowMasters{
                    ID
                    WORKFLOWNAME
                    MODULEID
                    MODULE_TYPE{
                        ID
                        MODULENAME
                    }

                    CREATEDBYID
                    LASTMODIFIEDBYID
                    DATECREATED
                    DATELASTMODIFIED
                }
            }
        `;

        API.getData(getdata)
            .then(({ data }) => this.setState({
                data: data.data.WorkflowMasters
            })).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    populateTypes() {
        const getdata = `
            query {
                Modules {
                    ID,
                    MODULENAME
                }
            }
        `;

        API.getData(getdata)
        .then(({ data }) => this.setState({
            searchdata: data.data.Modules
        })).catch(error => {
            if (error.response || error.request) {
                toastr.error('Error Loading Data', 'Failed to Module Type Load Data');
            }
        })
    }

    handleSubmit = (event) => {
        event.preventDefault();

        const form = `
        mutation{
            addWorkflowMaster(
                WORKFLOWNAME:"${this.state.WORKFLOWNAME}",
                MODULEID:${this.state.MODULEID},                
                CREATEDBYID:${this.state.CREATEDBYID},
                LASTMODIFIEDBYID:${this.state.LASTMODIFIEDBYID},
                DATECREATED:"${this.state.DATECREATED}",
                DATELASTMODIFIED:"${this.state.DATELASTMODIFIED}"
                ){
                ID
            }
        }
        `;
        console.log(form);
        const formEdit = `
        mutation{
            updateWorkflowMaster(
                ID:${this.state.ID},
                WORKFLOWNAME:"${this.state.WORKFLOWNAME}",
                MODULEID:${this.state.MODULEID},
                LASTMODIFIEDBYID:${this.state.LASTMODIFIEDBYID},
                DATELASTMODIFIED:"${this.state.DATELASTMODIFIED}"
                ){
                ID
            }
        }
        `;

        if (this.state.InsertUpdate === 'Insert') {
            API.create(form)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.success('Create Record', 'Record Created Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        } else {
            API.update(formEdit)
                .then(() => {
                    this.clearData();
                    this.handleHide();
                    this.populateData();
                    toastr.info('Update Record', 'Record Updated Successfully');
                }).catch(error => {
                    if (error.response || error.request) {
                        toastr.error('Error Loading Data', 'Failed to Load Data');
                    }
                })
        }

    }

    handleDelete = (event) => {
        event.preventDefault();

        const formDelete = `
        mutation{
            deleteWorkflowMaster(
                ID:${this.state.ID}
                ){
                ID
            }
        }
        `;

        API.remove(formDelete)
            .then(() => {
                this.handleHideDelete();
                this.populateData();
                toastr.warning('Delete Record', 'Record Deleted Successfully');
            }).catch(error => {
                if (error.response || error.request) {
                    toastr.error('Error Loading Data', 'Failed to Load Data');
                }
            })

    }

    handleCloseSearch = (e) => {

        this.setState({
            MODULEID: e.ID,
            MODULETYPEDESCRIPTION: e.MODULENAME,
            opensearch: false,
        });
    };

    handleEditData = (e) => {
        this.setState({
            ID: e.ID,
            WORKFLOWNAME: e.WORKFLOWNAME,
            MODULEID: e.MODULETYPEID,
            MODULETYPEDESCRIPTION:e.MODULE_TYPE.MODULENAME,
            show: true,
            InsertUpdate: 'Update'
        })
    }

    handleDeleteData = (e) => {
        this.setState({
            ID: e.ID,
            WORKFLOWNAME: e.WORKFLOWNAME,
            showdelete: true,
            InsertUpdate: 'Delete'
        })
    }

    clearData() {
        this.setState({
            WORKFLOWNAME: '',
            MODULETYPEID: '',
            MODULETYPEDESCRIPTION:''
        })
    }

    handleFilter = (e) => {
        let value = e.target.value
        let data = this.state.data
        let filteredData = this.state.filteredData
        this.setState({ value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.MODULE_TYPE.MODULENAME.toString().toLowerCase().startsWith(value.toLowerCase()) || 
                    item.WORKFLOWNAME.toString().toLowerCase().startsWith(value.toLowerCase())
                    

                let includesCondition =
                    item.MODULE_TYPE.MODULENAME.toString().toLowerCase().includes(value.toLowerCase()) ||
                    item.WORKFLOWNAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    handleSearchFilter = (e) => {
        let value = e.target.value
        let data = this.state.searchdata
        let filteredData = this.state.searchFilteredData
        this.setState({ searchvalue: value });

        if (value.length) {
            filteredData = data.filter(item => {
                let startsWithCondition =
                    item.MODULENAME.toString().toLowerCase().startsWith(value.toLowerCase())

                let includesCondition =
                    item.MODULENAME.toString().toLowerCase().includes(value.toLowerCase())

                if (startsWithCondition) {
                    return startsWithCondition
                } else if (!startsWithCondition && includesCondition) {
                    return includesCondition
                } else return null
            })
            this.setState({ filteredData })
        }
    }

    render() {
        const { data, value, filteredData, searchdata, searchvalue } = this.state
        return (
            <>
                <Helmet><title>Workflow Master</title></Helmet>

                <React.Fragment>
                    <Breadcrumbs
                        breadCrumbTitle="Admin"
                        breadCrumbParent="Setup"
                        breadCrumbActive="Workflow Master"
                    />
                    <Row>
                        <Col sm="12">
                            <Card>
                                <CardHeader>
                                    <CardTitle>Workflow Master</CardTitle>
                                </CardHeader>
                                <CardBody className="rdt_Wrapper">

                                    <ModuleTypeSearch
                                        handleSearchModuleType={this.handleCloseSearch}
                                        opensearch={this.state.opensearch}
                                        handleHideSearch={this.handleHideSearch}
                                        value={searchvalue}
                                        data={searchdata}
                                        handleFilter={this.handleSearchFilter}
                                        filteredData={filteredData}
                                    />

                                    <WorkflowMasterTable
                                        handleEdit={this.handleEditData}
                                        handleDelete={this.handleDeleteData}
                                        value={value}
                                        data={data}
                                        filteredData={filteredData}
                                        handleShow={this.handleShow}
                                        handleFilter={this.handleFilter}
                                    />

                                    <WorkflowMasterModal
                                        show={this.state.show}
                                        handleHide={this.handleHide}
                                        InsertUpdate={this.state.InsertUpdate}
                                        handleSubmit={this.handleSubmit}
                                        handleShowSearch={this.handleShowSearch}

                                         ModuleTypeID={this.state.MODULETYPEID}
                                         handleChangeModuleTypeID={e => this.setState({ MODULETYPEID: e.target.value })}

                                        ModuleTypeDescription={this.state.MODULETYPEDESCRIPTION}
                                        handleChangeModuleTypeDescription={e => this.setState({ MODULETYPEDESCRIPTION: e.target.value })}
                                        
                                        WorkflowName={this.state.WORKFLOWNAME}
                                        changeWorkflowName={e => this.setState({ WORKFLOWNAME: e.target.value })}

                                        showdelete={this.state.showdelete}
                                        handleHideDelete={this.handleHideDelete}
                                        handleDelete={this.handleDelete}

                                    />


                                </CardBody>
                            </Card>
                        </Col>
                    </Row>
                </React.Fragment>

            </>

        )
    }
}

const mapStateToProps = ({ auth: { user } }) => ({
    user
});

export default (connect(mapStateToProps)(WorkflowMaster));